<template>
    <div class="list-wrapper">
        <yd-navbar  title="Join a family">
            <router-link to="/" slot="left">
                <yd-navbar-back-icon>Home</yd-navbar-back-icon>
            </router-link>
            <router-link to="#"  @click.native="pop = true" slot="right">
                <span><p><img src="../../assets/icons/Filter.svg" alt=""></p></span>
            </router-link>
        </yd-navbar>

        <div class="family-wrapper">
            <div class="family-item" v-for="family,index in familys" :key="index">
                <h2>family name: {{family.family_name}}</h2>
                <p>district: {{family.district}}</p>
                <p>subway line: {{family.subwayline}}</p>
                <p>roomie number: {{family.roomie_number}}</p>
                <h2 class="profile-img hostguy">family host: 
                    <img :src='"http://"+family.small_landlord[0].profile' >
                </h2>

                <p class="roomie-wrapper">current: 
                    <p v-for="user,index in family.roomie_id" :key="index" class="roomie-item">
                        <span class="roomie-name">{{user.name}}</span>
                        <span class="profile-img roomie-img"><img :src='"http://"+ user.profile'></span>
                    </p>
                </p>
            
                <yd-button-group>
                    <yd-button @click.native="updateFamily(`${family.id}`) " size="large" type="primary" v-show="">
                        Block Button</yd-button>
                </yd-button-group>    
            </div>
        </div>

    <yd-popup v-model="pop" position="bottom" height="90%">
        <Vfilter ></Vfilter>
    </yd-popup>

    </div>
</template>
<script>
import { APIGetroomlist,APIgetfamily,APIfamilydetail,APIfamilyupdate }  from '../../api/api'
import Vfilter from './filter'
import  cookie  from '../../static/js/cookie'

export default {
    components:{
    Vfilter
    },
    data() {
        return {
            filtersdata: {
            },
            pop:false,
            familys: {},
            apply:true,
            myid:'',
            
        }
    },
    computed:{
       
    },
    methods:{
        GetApartments(){
            APIgetfamily().then(
                res => {
                    console.log(res.data)
                    this.familys = res.data
                }
            ).catch(err => {
                console.log(err)
            })
        }

    },
    mounted() {
        this.GetApartments(1)
    }
}
</script>
<style lang="stylus" scoped>
.family-item
    margin-bottom 10px
    border 1px solid #333
    .profile-img
        display inline-block
        position fixed
        img
            width 100px
            height 100px
            border-radius 50%

</style>






